  const splitIntoLines = (text, maxLineLength) => {
    const words = text.split(" ");
    const lines = [];
    const newLineIndex = [];
    let currentLine = words[0];
    
  
    for (let i = 1; i < words.length; i++) {
      if (currentLine.length + words[i].length + 1 <= maxLineLength) {
        currentLine += " " + words[i];
      } else {
        newLineIndex.push(i);
        lines.push(currentLine);
        currentLine = words[i];
      }
    }
    //newLineIndex.push(i);
    lines.push(currentLine); // add the last line
  
    return newLineIndex;
  };
  
  const findNthWordIndex = (str, n) => {
    // Split the string into an array of words
    const words = str.split(' ');
  
    // Get the nth word
    const nthWord = words[n - 1];
  
    // Find the index of the nth word in the original string
    const index = str.indexOf(nthWord);
  
    return index;
  }
  

